<template>
  <view class="container">
    <!-- 问候卡片 -->
    <view class="greeting-card">
      <text class="greeting-title">{{ greeting }}</text>
      <text class="greeting-date">{{ currentDate }}</text>
      <view class="efficiency-score">
        <view class="score-left">
          <text class="score-label">今日效率评分</text>
          <view class="score-value">
            <text class="score-number">{{ efficiencyScore }}</text>
            <view class="score-trend">
              <uni-icons
                v-if="scoreTrend > 0"
                type="arrow-up"
                size="14"
                color="#34d399"
              ></uni-icons>
              <uni-icons
                v-else-if="scoreTrend < 0"
                type="arrow-down"
                size="14"
                color="#ef4444"
              ></uni-icons>
              <text
                :class="
                  scoreTrend > 0
                    ? 'trend-up'
                    : scoreTrend < 0
                    ? 'trend-down'
                    : ''
                "
              >
                {{ scoreTrend > 0 ? "+" : "" }}{{ scoreTrend }}%
              </text>
            </view>
          </view>
        </view>
        <view class="score-icon">
          <uni-icons type="fire" size="24" color="#f59e0b"></uni-icons>
        </view>
      </view>
    </view>

    <!-- 树木养成区域 -->
    <view class="tree-section">
      <view class="tree-container">
        <image
          class="tree-gif"
          :style="{ transform: `scale(${0.8 + treeLevel * 0.1})` }"
          src="/static/tree-1.gif"
          mode="aspectFit"
        ></image>
        <view class="tree-ground"></view>
      </view>
      <view class="level-info">
        <text class="level-text">成长等级 {{ treeLevel }}</text>
        <view class="exp-bar">
          <view
            class="exp-progress"
            :style="{ width: `${(currentExp / levelUpExp) * 100}%` }"
          ></view>
        </view>
        <text class="exp-text">{{ currentExp }}/{{ levelUpExp }} 分钟</text>
      </view>
    </view>

    <!-- 快捷操作 -->
    <view class="quick-actions">
      <view class="action-btn" @tap="startFocus">
        <uni-icons type="clock" size="24" color="#4f46e5"></uni-icons>
        <text class="action-text">开始专注</text>
      </view>
      <view class="action-btn" @tap="addTask">
        <uni-icons type="plus" size="24" color="#4f46e5"></uni-icons>
        <text class="action-text">新建任务</text>
      </view>
      <view class="action-btn" @tap="viewCalendar">
        <uni-icons type="calendar" size="24" color="#4f46e5"></uni-icons>
        <text class="action-text">查看日历</text>
      </view>
      <view class="action-btn" @tap="viewStats">
        <uni-icons type="chart" size="24" color="#4f46e5"></uni-icons>
        <text class="action-text">效率分析</text>
      </view>
    </view>

    <!-- 今日任务 -->
    <view class="card task-card">
      <view class="card-header">
        <text class="card-title">今日任务</text>
        <text class="task-progress"
          >{{ completedTasks }}/{{ todayTasks.length }} 已完成</text
        >
      </view>
      <view class="task-list">
        <template v-if="todayTasks.length > 0">
          <view
            class="task-item"
            v-for="(task, index) in todayTasks"
            :key="index"
          >
            <view
              :class="['task-checkbox', task.completed ? 'checked' : '']"
              @tap="toggleTask(task.id)"
            >
              <uni-icons
                v-if="task.completed"
                type="checkmark"
                size="12"
                color="#ffffff"
              ></uni-icons>
            </view>
            <view class="task-content">
              <text
                :class="['task-title', task.completed ? 'completed' : '']"
                >{{ task.title }}</text
              >
              <text class="task-time" v-if="task.startTime && task.endTime"
                >{{ task.startTime }} - {{ task.endTime }}</text
              >
            </view>
            <view :class="['task-tag', `tag-${task.category || 'default'}`]">{{
              task.category || "其他"
            }}</view>
          </view>
        </template>
        <view class="empty-tasks" v-else>
          <image
            class="empty-image"
            src="/static/images/empty-tasks.png"
            mode="aspectFit"
          ></image>
          <text class="empty-text">还没有待办任务哦</text>
        </view>
      </view>
    </view>

    <!-- 本周生产力 -->
    <view class="card productivity-card">
      <view class="card-header">
        <text class="card-title">本周生产力</text>
        <text class="view-more" @tap="viewStats">查看详情</text>
      </view>
      <view class="productivity-chart">
        <view
          class="chart-column"
          v-for="(day, index) in weeklyProductivity"
          :key="index"
        >
          <view class="chart-bar" :style="{ height: `${day.value}%` }"></view>
          <text class="chart-label">{{ day.label }}</text>
        </view>
      </view>
    </view>
  </view>
</template>

<script setup>
import { ref, reactive, onMounted } from "vue"
import { onShow } from "@dcloudio/uni-app"
// 响应式状态
const greeting = ref("下午好，今天也要加油哦")
const currentDate = ref("")
const efficiencyScore = ref(92)
const scoreTrend = ref(5)
const completedTasks = ref(0)
const focusTime = ref("0h")
const todayTasks = ref([])

// 树木养成系统数据
const treeLevel = ref(1)
const currentExp = ref(0)
const levelUpExp = ref(60) // 每60分钟升一级
const showCelebration = ref(false)
const totalFocusMinutes = ref(0)

// 本周生产力数据
const weeklyProductivity = ref([
  { label: "一", value: 65 },
  { label: "二", value: 80 },
  { label: "三", value: 45 },
  { label: "四", value: 70 },
  { label: "五", value: 85 },
  { label: "六", value: 30 },
  { label: "日", value: 20 },
])

// 生命周期钩子
onMounted(() => {
  setGreeting()
  setCurrentDate()
  loadTasks()
  loadTreeData()
})

onShow(() => {
  loadTasks()
})

// 方法
function setGreeting() {
  const hour = new Date().getHours()
  let greetingText = ""
  if (hour < 6) {
    greetingText = "凌晨好"
  } else if (hour < 11) {
    greetingText = "早上好"
  } else if (hour < 14) {
    greetingText = "中午好"
  } else if (hour < 18) {
    greetingText = "下午好"
  } else {
    greetingText = "晚上好"
  }
  greeting.value = `${greetingText}，今天也要加油哦`
}

function setCurrentDate() {
  const date = new Date()
  const year = date.getFullYear()
  const month = date.getMonth() + 1
  const day = date.getDate()
  const weekDay = ["日", "一", "二", "三", "四", "五", "六"][date.getDay()]
  currentDate.value = `${year}年${month}月${day}日 星期${weekDay}`
}

function toggleTask(taskId) {
  const task = todayTasks.value.find((t) => t.id === taskId)
  if (task) {
    task.completed = !task.completed
    completedTasks.value = todayTasks.value.filter((t) => t.completed).length

    // 保存到本地存储
    const allTasks = uni.getStorageSync("tasks") || []
    const taskIndex = allTasks.findIndex((t) => t.id === taskId)
    if (taskIndex !== -1) {
      allTasks[taskIndex].completed = task.completed
      uni.setStorageSync("tasks", allTasks)
    }
  }
}

function startFocus() {
  uni.navigateTo({
    url: "/pages/focus/focus",
  })
}

function addTask() {
  uni.navigateTo({
    url: "/pages/addTask/addTask",
  })
}

function viewCalendar() {
  uni.showToast({
    title: "日历功能开发中",
    icon: "none",
  })
}

function viewStats() {
  uni.navigateTo({
    url: "/pages/analytics/analytics",
  })
}

function viewAllTasks() {
  uni.navigateTo({
    url: "/pages/taskList/taskList",
  })
}

function loadTasks() {
  const tasks = uni.getStorageSync("tasks") || []

  // 筛选今天的任务
  const today = new Date().toISOString().split("T")[0]
  todayTasks.value = tasks.filter(
    (task) => task.date === today || (!task.date && !task.completed)
  )

  completedTasks.value = todayTasks.value.filter((t) => t.completed).length
}

function loadTreeData() {
  const treeData = uni.getStorageSync("growingTree") || {
    level: 1,
    experience: 0,
    totalMinutes: 0,
    nextLevelExp: 60,
  }

  treeLevel.value = treeData.level
  currentExp.value = treeData.experience
  levelUpExp.value = treeData.nextLevelExp
  totalFocusMinutes.value = treeData.totalMinutes
}

function updateTreeExp(minutes) {
  // 更新经验值
  currentExp.value += minutes
  totalFocusMinutes.value += minutes

  // 检查是否可以升级
  if (currentExp.value >= levelUpExp.value) {
    // 计算可以升几级
    let levelUp = 0
    while (currentExp.value >= levelUpExp.value) {
      currentExp.value -= levelUpExp.value
      levelUp++
      // 下一级所需经验值增加 (简单公式：下一级 = 当前级 * 1.5)
      levelUpExp.value = Math.round(levelUpExp.value * 1.5)
    }

    treeLevel.value += levelUp

    // 显示升级动画
    showCelebration.value = true
    setTimeout(() => {
      showCelebration.value = false
    }, 3000)

    // 播放声音
    const innerAudioContext = uni.createInnerAudioContext()
    innerAudioContext.src = "/static/audio/level-up.mp3"
    innerAudioContext.play()
  }

  // 保存到本地
  uni.setStorageSync("growingTree", {
    level: treeLevel.value,
    experience: currentExp.value,
    totalMinutes: totalFocusMinutes.value,
    nextLevelExp: levelUpExp.value,
  })
}
</script>

